@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap);
.BasicButton_basicButton__O0PhB {
  padding: 5px;
  font-size: 14px;
  color: var(--blue);
  background-color: rgb(173 185 236 / 0.2);
  border: 1px solid var(--blue);
  border-radius: var(--radius);
  cursor: pointer;
  transition: .3s ease-out;
}

.BasicButton_basicButton__O0PhB:hover {
  background: var(--blue);
  color: #fff;
}

.BasicInput_basicInput__1lXGf {
  width: 100%;
  margin-bottom: 10px;
  padding: 5px 10px;
  border: 1px solid var(--blue);
  border-radius: var(--radius);
}

.BasicSelect_basicSelect__E-XVq {
  padding: 5px;
  border-radius: var(--radius);
}

:root {
  --radius: 3px;
  --blue: #5a6b88;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Roboto, sans-serif;
  font-size: 16px;
  color: #555;
}

h1, h2, h3, h4, h5, h6 {
  color: #777;
}

#root {
  display: flex;
  flex-direction: column;
  max-width: 700px;
  margin: auto;
  padding: 10px;
}

.post-form {
  padding: 10px 0;
}

.post-list {
  padding: 10px 0;
}

.post-list__header {
  padding: 10px;
  text-align: center;
}

.post {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 15px;
  padding: 15px;
  /*border: 1px solid var(--blue);*/
  border-radius: 3px;
  border-radius: var(--radius);
  box-shadow: 0 0 3px #5a6b88;
  box-shadow: 0 0 3px var(--blue);
}

